.Quote {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h1 {
    font-size: 30px;
    font-family: "Press Start 2P", cursive;
    padding-left: 10px;
    color: white;
    text-shadow: 0 0 10px black;
    padding: 20px 0;
    line-height: 35px;
  }
  p {
    font-size: 30px;
    color: rgb(202, 202, 202);
    text-shadow: 0 0 10px black;
    align-self: flex-end;
    font-family: "Gemunu Libre", sans-serif;
  }
}
@media (min-width: 500px) {
  .Quote {
    width: 70%;
  }
}
@media (max-width: 500px) {
  .Quote {
    width: 85%;

    h1 {
      font-size: 25px;
      width: 90%;
      text-align: center;
    }
  }
}
