.Affiliation {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  h1 {
    font-size: 30px;
    font-family: "Press Start 2P", cursive;
    padding-left: 10px;
    color: white;
    text-shadow: 0 0 10px black;
    padding: 20px 0;
    line-height: 35px;
  }
  .AffiliationContent {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    img {
      float: left;
      width: 200px;
    }
    .AffiliationText {
      h2 {
        font-size: 20px;
        font-family: "Press Start 2P", cursive;
        padding-left: 10px;
        color: rgb(236, 235, 235);
        text-shadow: 0 0 10px black;
        padding: 20px 0;
        line-height: 35px;
      }
      p {
        font-size: 20px;
        color: rgb(202, 202, 202);
        text-shadow: 0 0 10px black;
        font-family: "Gemunu Libre", sans-serif;
      }
    }
  }
}
@media (min-width: 500px) {
  .Affiliation {
    width: 70%;
    .AffiliationContent {
      width: 80%;
    }
    .AffiliationText {
      margin-left: 50px;
    }
  }
}
@media (max-width: 500px) {
  .Affiliation {
    width: 90%;
    h1 {
      font-size: 25px;
    }
    .AffiliationContent {
      flex-direction: column;
      width: 100%;
    }
    .AffiliationText {
      text-align: center;
    }
  }
}
